body {
    margin: 0 !important;
    padding: 0 !important;
}

.page-body{
  margin-bottom: 5%;
}

.topHeader{
  margin:auto;
}

.bottomText{
  padding-left:10px;
  text-align: left;
  vertical-align:bottom;
  margin-bottom: 5px;
}

.topHeader h1{
  font-size: 50px;
}

/* Add a black background color to the top navigation */

.topnav {
    background-color: #4B9CD3;
    overflow: hidden;
}

/* Style the links inside the navigation bar */

.topnav a {
    float: left;
    display: block;
    color: #f2f2f2;
    text-align: center;
    padding: 14px 30px;
    text-decoration: none;
    font-size: 17px;
}

/* Change the color of links on hover */

.topnav a:hover {
    background-color: #ddd;
    color: black;
}

/* Hide the link that should open and close the topnav on small screens */

.topnav .icon {
    display: none;
}

.formFields{
  text-align:center;
  padding-top:5px;
}

.leftField{
	float: left;
	padding-left:15%;
}

.rightField{
	float: right;
	padding-right:15%;
}

.categoryFormat{
	padding-bottom:50px;
}

input{
	width: 200px;
}

select{
	width:200px;
}

.formNoBorder{
	margin:auto;
	padding-left:10%;
	padding-right:10%;
}

.formNoBorder td{
	text-align:left;
	padding-right:20px;
	padding-bottom:10px;
}

.middleSpace{
padding-left:150%;
}

.descriptionBox{
	width: 100%;
}

.itemsTable{
    width:90%;
	margin:auto;
	padding-left:10%;
    padding-right:10%;
    border-collapse: collapse;
}

.itemsTable th{
    text-align: center;
    background-color: #4B9CD3;
    padding: 8px;
}

.itemsTable td, th{
    border: 1px solid rgb(34, 88, 150);
    padding: 3px;
	text-align:left;
}

.itemsTable tr:nth-child(odd) {
    background-color: rgb(162, 218, 255);
}

.itemsTable tr:hover{
    background-color: rgb(253, 198, 97);
    cursor: pointer;
}

.modal-mask {
    position: fixed;
    z-index: 9998;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .5);
    display: table;
    -webkit-transition: opacity .3s ease;
    transition: opacity .3s ease;
  }

.modal-wrapper {
    display: table-cell;
    vertical-align: middle;
  }

.modal-container {
    width: 300px;
    margin: 0px auto;
    padding: 20px 30px;
    background-color: #fff;
    border-radius: 2px;
    -webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
            box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    font-family: Helvetica, Arial, sans-serif;
    overflow:hidden;
  }

.modal-header h2 {
    margin-top: 0;
  }

.modal-body {
    margin: 20px 0;
  }

.modal-footer button {
    float:right;
  }

.modal-enter {
    opacity: 0;
  }

.modal-leave-active {
    opacity: 0;
  }

.modal-enter .modal-container,
  .modal-leave-active .modal-container {
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }

.item-detail-list {
    list-style-type: none;
    padding-left: 0;
    margin-left: 0;
